main.start-page {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 80vh;
}

.play-buttons, .choosegame-buttons {
    grid-gap: 10px;
    gap: 10px;
}
.play-buttons button, .choosegame-buttons button {
    background-color: var(--colorAction) !important;
    color: var(--colorInvert) !important;
    border-radius: 10px !important;
    box-shadow: none !important;
}
.play-buttons button {
    font-size: 21px !important;
    font-weight: 600 !important;
}
.choosegame-buttons button {
    font-size: 14px !important;
}
.play-buttons button.nobg-button, .choosegame-buttons button {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 5px !important;
    gap: 5px !important;
    background-color: transparent !important;
    color: var(--color) !important;
    box-shadow: inset 0px 0px 0px 1px #d8d8da !important;
}
.play-buttons button.nobg-button.noborder {
    box-shadow: none !important;
}
.play-buttons button.nobg-button.noborder path {
    color: var(--helpColor) !important;
}
/* .players-box {
    background-color: var(--bgSecondHelper) !important;
    padding: 10px;
} */
.players-box input {
    width: 70%;
    border: none;
    box-shadow: inset 0px 0px 0px 1px #d8d8da !important;
    font-size: 18px;
    padding: 0.5rem 1rem;
}
.player-li {
    font-size: large;
    margin: 10px 0px;
}
.question {
    font-size: 1.5rem;
    font-weight: 400;
}
.play-buttons button.disabled {
    background-color: var(--helpColor) !important;
}
.feedback svg {
    width: 20px;
    height: 20px;
}
.penalty-blur {
    color: transparent;
    -webkit-filter: blur(5px) drop-shadow(0px 0px 2px black);
            filter: blur(5px) drop-shadow(0px 0px 2px black);
    user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
}
.play-buttons button.nobg {
    background-color: transparent !important;
}
.good-message {
    font-style: italic;
    font-weight: 500;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box !important;
    font-family: var(--font) !important;
    color: var(--color);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.MuiSwitch-thumb {
    color: unset !important;
}
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

/* ===== Scrollbar CSS ===== */
  /* Firefox */
  * {
    scrollbar-width: auto;
    scrollbar-color: #969696 #1c1c1c;
  }

  /* Chrome, Edge, and Safari */
  *::-webkit-scrollbar {
    width: 5px;
  }

  *::-webkit-scrollbar-track {
    background: #ffffff;
  }

  *::-webkit-scrollbar-thumb {
    background-color: #b9b9b9;
    border-radius: 10px;
    border: 0px solid #ffffff;
  }
  ::-webkit-scrollbar-thumb:hover {
    background-color: #a5a5a5;
  }

